export default {
  name: "productOptionMixin",
  data() {
    return {
      showError: false,
    };
  },
  methods: {
    handleError() {
      if (this.is_require === "0") return;
      this.showError =
        this.selected[this.option_id] instanceof Array
          ? this.selected[this.option_id].length === 0
          : !this.selected[this.option_id];
    },
  },
  mounted() {
    this.handleError();
  },
  watch: {
    selected() {
      this.handleError();
    },
  },
};
