<template>
  <AddToCartPreviewModalRepresentation
    v-if="isShowModal"
    :isShowModal="isShowModal"
    :hideModal="closeAndReloadCart"
    :imageUrl="imageUrl"
    :productName="productOptions.request.product_name"
    :error="error"
    :description="productOptions.request.product_short_description"
    :updateQty="updateQty"
    :quantity="quantity"
    :disableQuantity="productOptions.hide_custom_option_qty"
    :addToCart="onAddToCart"
    :addingToCart="adding"
    :totalPrice="totalPrice() | formatISK"
  >
    <component
      v-for="option in productOptions.options"
      :key="option.option_id"
      :is="'Field' + option.type[0].toUpperCase() + option.type.slice(1)"
      :selected="selected"
      itemClass="list-item"
      v-bind="option"
      v-model="selected"
      :isSubmitted="isSubmitted"
    ></component>
  </AddToCartPreviewModalRepresentation>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import CartItemMixin from "@/mixins/CartItemMixin";
import FieldRadio from "@/components/FieldRadio";
import FieldCheckbox from "@/components/FieldCheckbox";
import FieldDrop_down from "@/components/FieldDrop_down";
import FieldField from "@/components/FieldField";
import AddToCartPreviewModalRepresentation from "@/components/AddToCartPreviewModalRepresentation";
import { MODAL_KEY_PRODUCT_OPTION } from "@/store/modalModule";
import get from "@/util/get";

export default {
  name: "TheProductOptionsModal",
  mixins: [CartItemMixin],
  components: {
    FieldRadio,
    FieldCheckbox,
    FieldDrop_down,
    FieldField,
    AddToCartPreviewModalRepresentation,
  },
  data() {
    return {
      selected: {},
      quantity: 1,
      usingCustomNotification: false, // use custom notification instead of system toast
      isSubmitted: false,
    };
  },
  computed: {
    ...mapGetters(["productOptions"]),
    isShowModal() {
      return !!get(this.productOptions, "options");
    },
    imageUrl: function() {
      let image = get(this.productOptions, "request.product_image");
      if (image && image !== "no_selection") {
        if (image.indexOf("https://") === -1) {
          return "https://images.aha.is/image/upload/products/" + image;
        }
        if (image.startsWith === "https://cdn.aha.is/media/catalog/product/") {
          return (
            "https://images.aha.is/image/upload/products/" +
            image.replace("https://cdn.aha.is/media/catalog/product/", "")
          );
        }
        return image;
      }
      return false;
    },
    options_price: function() {
      let options = Object.values(this.productOptions.options);
      if (Object.keys(options[0]).includes("values")) {
        options = options.flatMap(option => option.values);

        return (
          Object.values(this.selected)
            .flat()
            .flatMap(option =>
              options
                .filter(v => v)
                .filter(({ option_type_id }) => option_type_id === option)
            )
            .reduce((result, { price }) => result + parseInt(price), 0) *
          (this.quantity || 1)
        );
      } else {
        return parseInt(options[0]["default_price"]);
      }
    },
  },
  methods: {
    ...mapActions(["loadCart", "toggleModal"]),
    closeAndReloadCart() {
      const { close, loadCart } = this;
      close();
      loadCart();
      this.isSubmitted = false;
    },
    close: function() {
      this.selected = {};
      this.quantity = 1;
      this.error = false;
      this.toggleModal({
        modalKey: MODAL_KEY_PRODUCT_OPTION,
        isVisible: false,
        options: {},
      });
    },
    onAddToCart() {
      this.isSubmitted = true;
      this.isAllRequiredOptionsFilled();
      if (this.isAllRequiredOptionsFilled()) {
        return this.addToCart();
      }
    },
    totalPrice: function() {
      return this.total + this.options_price;
    },
    isAllRequiredOptionsFilled() {
      const { productOptions, selected } = this;
      if (!productOptions.options) return;
      const optionsList = Object.entries(productOptions.options);
      const requiredOptionKeys = optionsList
        .filter(([key, value]) => value.is_require === "1")
        .map(([key]) => key);

      if (!requiredOptionKeys.length) return true;

      return requiredOptionKeys.every(optionKey => selected[optionKey]);
    },
  },
  mounted() {
    this.quantity = 1;
  },
  destroyed() {
    this.isSubmitted = false;
  },
};
</script>

