<template>
  <fieldset v-show="showBaseDependent">
    <h4>{{ default_title }}</h4>
    <!-- TODO: get placeholder from product short_description?? -->
    <div :class="type + '-design'">
      <input
        class="app-input"
        :name="'option-' + option_id"
        :required="is_require"
        @input="update(option_id, $event.target.value)"
      />
    </div>
    <span class="error error-danger" v-if="isSubmitted && showError">{{
      $t("requireOption", { error: `"${default_title}"` })
    }}</span>
  </fieldset>
</template>

<script>
import { mapGetters } from "vuex";
import productOptionMixin from "@/mixins/productOptionMixin";

export default {
  name: "FieldField",
  mixins: [productOptionMixin],
  computed: {
    ...mapGetters(["productOptions"]),
    showBaseDependent() {
      if (this.$route.path !== "/checkout") return true;
      const options = this.productOptions.options;
      let isDropdown = Object.keys(options)
        .map(item => {
          if (options[item].type === "drop_down") return { ...options[item] };
        })
        .filter(v => v)[0];
      if (isDropdown) {
        return !!this.selected[isDropdown.option_id];
      }
      return true;
    },
  },
  methods: {
    update(key, value) {
      this.$emit("input", { ...this.selected, [key]: value });
    },
  },
  props: [
    "type",
    "default_title",
    "values",
    "option_id",
    "is_require",
    "selected",
    "isSubmitted",
  ],
};
</script>

