<template>
  <fieldset>
    <h4>{{ default_title }}</h4>
    <ul class="row">
      <li :class="itemClass" v-for="value in values" v-bind:key="value.option_type_id">
        <label
          class="field-check-box__label"
          :for="'option-' + value.option_id + '-' + value.option_type_id"
        >
          <div :class="type + '-design'">
            <input
              :type="type"
              :name="'option-' + option_id"
              :id="'option-' + value.option_id + '-' + value.option_type_id"
              :value="parseInt(value.option_type_id)"
              :required="is_require"
              @input="update(option_id, $event.target.value)"
            />
            <span class="checkmark" />
            {{ value.default_title }}
            <span>{{ optionPrice(value.price) }}</span>
          </div>
        </label>
      </li>
    </ul>
    <span
      class="error error-danger"
      v-if="isSubmitted && showError"
    >{{ $t("requireOption", { error: `"${default_title}"` }) }}</span>
  </fieldset>
</template>

<script>
import productOptionMixin from "@/mixins/productOptionMixin";
export default {
  name: "FieldCheckbox",
  mixins: [productOptionMixin],
  methods: {
    update(key, value) {
      !(key in this.selected)
        ? this.$emit("input", { ...this.selected, [key]: [value] })
        : this.selected[key].includes(value)
        ? this.$emit("input", {
            ...this.selected,
            [key]: this.selected[key].filter(opt => opt !== value),
          })
        : (this.selected[key].push(value), this.$emit("input", this.selected));
      this.handleError();
    },
    optionPrice(price) {
      return price !== "0.0000"
        ? `+${this.$options.filters.formatISK(parseInt(price))}`
        : "";
    },
  },
  props: [
    "type",
    "default_title",
    "values",
    "option_id",
    "is_require",
    "selected",
    "itemClass",
    "isSubmitted",
  ],
};
</script>

