<template>
  <fieldset>
    <h4>{{ default_title }}</h4>
    <Select
      class="field-drop-down__select"
      :options="options"
      :selectedOption="selectedOption || defaultOption"
      :onSelectOption="onSelectOption"
    />
    <span class="error error-danger" v-if="isSubmitted && showError">{{
      $t("requireOption", { error: `"${default_title}"` })
    }}</span>
  </fieldset>
</template>
<i18n>
  {
    "is": {
      "select": "Veldu"
    },
    "en": {
      "select": "Select"
    }
  }
</i18n>

<script>
import productOptionMixin from "@/mixins/productOptionMixin";
import Select from "@/components/Select";

export default {
  name: "FieldDrop_down",
  components: {
    Select,
  },
  mixins: [productOptionMixin],
  data() {
    return {
      selectedOption: undefined,
    };
  },
  methods: {
    onSelectOption(selectedOption) {
      const { update, option_id } = this;
      this.selectedOption = selectedOption
      update(option_id, selectedOption.value);
    },
    update(key, value) {
      this.$emit("input", { ...this.selected, [key]: value });
    },
  },
  props: [
    "type",
    "default_title",
    "values",
    "option_id",
    "is_require",
    "selected",
    "isSubmitted",
  ],
  computed: {
    defaultOption() {
      return {
        label: this.$t("select"),
        value: "",
      };
    },
    options() {
      return [
        this.defaultOption,
        ...this.values.map(({ default_title, option_type_id }) => ({
          label: default_title,
          value: option_type_id,
        })),
      ];
    },
  },
};
</script>

<style lang="scss" scoped>
h4 {
  margin-bottom: 15px;
}
.field-drop-down__select {
  margin-bottom: 15px;
}

@media screen and (max-width: 767px) {
  .field-drop-down__select {
    font-size: 16px;
    height: unset;
  }
}
</style>
